<template>
	<div class="RecommendList">
		
		<h2>你可能还会喜欢</h2>
		<div class="list">
			
			<div style="background-color: white;margin: 10px 0;border-radius: 10px;" v-for="(item,id) in data" :key="id">
				<img
					:src="data[id].img">
				<p class="title">{{ data[id].title }}</p>
				<button class="coupon">{{ data[id].coupon }}</button>
				<p class="price">
					<span style="font-size: 16px; font-weight: normal;">券后 </span>
					￥{{ data[id].price }}
				</p>
			</div>
			
			
			
		
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'RecommendList',
		data(){
			return {
				data:[]
			}
		},
		mounted(){
			this.getREcommendListDate()
		},
		methods:{
			getREcommendListDate(){
				axios.get('https://www.fastmock.site/mock/759aba4bef0b02794e330cccc1c88555/beers/goods').then(res => {
					this.data = res.data;
				})
			}
		}
	}
</script>

<style scoped>
	.RecommendList,.list {
		background-color: rgb(246, 246, 246) !important;
	}
	.RecommendList h2 {
		text-align: left;
		padding-left: 10px;
		color: rgb(1, 1, 1);
		font-size: 19px;
		font-weight: 550;
		letter-spacing: 1px;
	}

	.list {
		margin: 0px 5px;
		column-count: 2;
	}

	.list img {
		width: 100%;
		border-radius: 5px 5px 0px 0px;
	}

	.title {
		width: 95%;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-line-clamp: 2;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		color: black;
		font-size: 17px;
		font-weight: 520;
		padding-left: 5px;
		margin-bottom: 5px;
	}

	.coupon {
		padding: 2px 5px;
		border: 1px solid rgb(242, 53, 98);
		color: rgb(242, 53, 98);
		background-color: rgb(255, 255, 255);
		font-size: 12px;
		margin-left: 10px;
		margin-bottom: 10px;
	}

	.price {
		padding-left: 8px;
		font-size: 18px;
		color: rgb(242, 53, 98);
		font-weight: bold;

	}
