<template>
	<div>
		<div class="head">
			<div class="serchOrder">
				<img src="../assets/fanhui.b144fa50.svg" alt="serch">
				<div class="serchGroup">
					<input type="text" placeholder="搜索订单" v-model="serch">
					<img class="serchImg" src="../assets/sousuo.c39faf05.svg" alt="搜索图标">
				</div>
				<img src="../assets/gengduo.becc0ed3.svg" alt="gduo">
			</div>
			<ul>
				<li 
				:class="{activeLi : currentpage === 1}"
				@click="currentpage = 1"
				>全部</li>
				<li :class="{activeLi : currentpage === 2}" @click="currentpage = 2">待支付</li>
				<li :class="{activeLi : currentpage === 3}" @click="currentpage = 3">待发货</li>
				<li :class="{activeLi : currentpage === 4}" @click="currentpage = 4">待收货/使用</li>
				<li :class="{activeLi : currentpage === 5}" @click="currentpage = 5">评价</li>
				<li :class="{activeLi : currentpage === 6}" @click="currentpage = 6">退款</li>
			</ul>
		</div>
		<OrderList></OrderList>
		<RecommendList></RecommendList>
	</div>
</template>

<script>
import axios from 'axios'
import RecommendList from './RecommendList.vue'
import OrderList from './OrderList.vue'
export default {
  name: 'Myorder',
  components:{
	  RecommendList,
	  OrderList
  },
  data(){
	  return {
		  currentpage:1,
		  serch:'',
		  results:[]
		  
		  
	  }
  },
  mounted(){
	  this.getData()
	  
  },
  watch:{
	  currentpage(){
		  this.getData(),
		  this.props()
	  }
  },
  methods:{
	  getData(){
		  axios.get('https://www.fastmock.site/mock/759aba4bef0b02794e330cccc1c88555/beers/order').then(res => {
			  let result = res.data;
			  
			  switch(this.currentpage) {
				  case 2 : result = result.filter(item => item.state == '待支付');
				  break;
				  case 3 : result = result.filter(item => item.state == '待发货');
				  break;
				  case 4 : result = result.filter(item => item.state == '待收货/使用');
				  break;
				  case 5 : result = result.filter(item => item.state == '评价');
				  break;
				  case 6 : result = result.filter(item => item.state == '退款');
				  break;
				  default:break;
				  
			  }
			   
			  this.results = result;
			  this.props()
			  // console.log(result)
			
		  })
		 
	  },
	props(){
		this.$bus.$emit('Data',this.results)
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.head {
	position: fixed;
	top: 0;
	width: 100%;
	background-color: white;
}
.serchOrder img {
	width: 30px;
	height: 30px;
}
.serchImg {
	width: 22px !important;
	height: 22px !important;
	position: absolute;
	left: 18px;
	top: 7px;
}
.serchOrder {
	height: 77px;
	padding: 20px 10px;
	display: flex;
	justify-content: space-around;
}
.serchGroup {
	width: 100%;
	position: relative;
}
.serchGroup input {
	width: 95%;
	height: 37px;
	border: 0;
	border-radius: 10px;
	font-size: 18px;
	padding: 2px 0 2px 40px;
	background-color: rgb(244, 244, 244);
}
.head ul {
	background-color: rgb(255, 255, 255);
	width: 100%;
	display: flex;
	overflow-x: auto;
	 /* 隐藏滚动条 */
	scrollbar-width: none; /* firefox */
	-ms-overflow-style: none; /* IE 10+ */
}
.head ul::-webkit-scrollbar {
	display: none;
}
.head ul>li {
	line-height: 30px;
	height: 30px;
	margin: 0px 15px 30px;
	font-size: 18px;
	float: left;
	flex-shrink: 0;
	position: relative;
}
.activeLi {
	color: black;
	font-weight: bold !important;
}
.activeLi::after {
	content: "";
	display: block;
	width: 50%;
	height: 3px;
	background-color: rgb(255, 36, 78);
	position: absolute;
	top: 36px;
	left: 25%;
}
</style>
